import sangre from "../../DB/analisis/sangre.json";
import orina from "../../DB/analisis/orina.json";
import AnalisisElem from "../../Components/AnalisisElmDesk";

export default function AnalisisDesk() {
    return <>

        <table id="t-1-d">
            <tbody>
                <tr>
                    <td>
                        <h3>ANALISIS DE SANGRE</h3>
                        <table>
                            <tbody>

                                {sangre.map(el => <AnalisisElem name={el} type={"sangre"} />)}
                            </tbody>
                        </table>
                    </td>
                    <td></td>
                    <td>
                        <h3>ANALISIS DE ORINA</h3>
                        <table>
                            <tbody>
                                {orina.map(el => <AnalisisElem name={el} type={"orina"} />)}
                            </tbody>
                        </table>
                    </td>
                </tr>
            </tbody>
        </table>
    </>
}