import { useDevice } from "../Context/is_mobile";
import AnalisisDesk from "../MComponents/Tablas/Desk";
import AnalisisMob from "../MComponents/Tablas/Mob";

export default function AnalisisPage(){
    const {device} =useDevice();
    return <>
        {
            device == "desk"
            ? <AnalisisDesk/>
            : <AnalisisMob/>
        }
    
    </>
}