import { useAnalisis } from "../Context/analisis";

export default function AnalisisElem({ name, type }) {
    const { analisis, updateAnalisis } = useAnalisis();

    const addAnalisis = (newA) => {
        newA[type].push(name)
        return newA
    }
    const deleteAnalisis = (newA) => {
        newA[type] = newA[type].filter(nw => nw != name);
        return newA
    }
    return <tr className="a-elm-d">
        <td><h4>{name.toUpperCase()}</h4></td>
        <td onClick={(e) => {
            let new_analisis = JSON.parse(JSON.stringify(analisis));

            if (e.target.className.includes("no-selected") || e.target.className == "") {
                e.target.className = "selected";
                new_analisis = addAnalisis(new_analisis)
            } else {
                e.target.className = "no-selected";
                new_analisis = deleteAnalisis(new_analisis)
            }
            
            updateAnalisis(new_analisis)
            
        }}></td>
    </tr>
}