import html2pdf from 'html2pdf.js';
import ReactDOMServer from 'react-dom/server';
import { useAnalisis } from '../Context/analisis';
import "../css/pdf.css"


const Template = ({ data }) => {


    const trs = [];
    let max = 0;
    if (data.sangre.length > data.orina.length) {
        max = data.sangre.length
    } else {
        max = data.orina.length
    }

    for (let i = 0; i < max; i++) {
        trs.push(
            <tr>
                <td>{(data.sangre[i] == undefined) ? ("-") : (data.sangre[i])}</td>
                <td>{(data.orina[i] == undefined) ? ("-") : (data.orina[i])}</td>
            </tr>)

    }

    return <>
        <div className='pdf-content'>
            <h2>ANALISIS A REALIZAR:</h2>
            <table>
                <tr>
                    <td>SANGRE</td>
                    <td>ORINA</td>
                </tr>
                {trs}
            </table>
        </div>
    </>
}

export default function ToPDF() {

    const { analisis } = useAnalisis();
    const HandleEvent = () => {

        const getDate =()=>{
            const date = new Date();
            return `${date.getDate()}_${date.getMonth()-1}_${date.getFullYear()}`
        }

        const htmlString = ReactDOMServer.renderToStaticMarkup(<Template data={analisis} />);
            const container = document.createElement('div');
            container.innerHTML = htmlString;
            document.body.appendChild(container);
            html2pdf().set({ filename: `Pedido_Analisis_${getDate()}.pdf` }).from(container).save().then(() => {
                document.body.removeChild(container); // limpiar después
            });


    }
    return <>
        <button onClick={HandleEvent}>EXPORTAR</button>
    </>
}