import { createContext, useContext, useState } from "react";

export const AnalisisContext = createContext();
export const useAnalisis = () => useContext(AnalisisContext);




const AnalisisProvider = ({ children }) => {
    const [analisis, updateAnalisis] = useState({ sangre: [], orina: [] });



    return <AnalisisContext.Provider value={{ analisis, updateAnalisis }}>{children}</AnalisisContext.Provider>
}

export default AnalisisProvider